export default {
    created() {
        this.getData()
    },
    data() {
        return {
            number: 0,
            data: null,
            outList:[]
        };
    },
    methods: {
        outTypeValue(value){
            switch (value) {
                case 0:
                    return "未处理";
                case 1:
                    return "已处理";
                case 2:
                    return "异常";
                default:
                    return "未知"
            }
        },
        outTagType(value){
            switch (value) {
                case 0:
                    return "info";
                case 1:
                    return "success";
                case 2:
                    return "error";
                default:
                    return "info"
            }
        },
        async getData() {
            // 任务列表
            await this.$u.get('/get_assets').then(
                res => {
                    if (res.errorCode !== 0) return;
                    this.data = res.data
                    this.number = res.data.wallet
                }
            )
            // 请求列表
            await this.$u.get('/get_out_order').then(res=>{
                if(res.errorCode!==0) return;
                this.outList=res.data;
            })
        },
        async out() {
            if ((this.number % 10) != 0 || this.number == 0) {
                uni.showToast({
                    title: "提现金额必须为10倍",
                    icon: 'none'
                })
                return;
            }
            // 请求提现
            await this.$u.post('/post_out_order', {
                amount: this.number
            }).then(res => {
                {
                    if (res.errorCode !== 0) return;
                    uni.showToast({
                        title: res.msg,
                        icon: 'none'
                    })
                    return;
                }
            })


        }
    }
}
